.burger {
  position: relative;
  width: 48px;
  height: 51px;
  display: none;
  border: none;
  padding: 14px;
  border-radius: 0;
  background-color: transparent;
  transition: transform 0.45s ease, position 0.35s ease;
  cursor: pointer;
}
.burger__line {
  position: absolute;
  left: 14px;
  top: 50%;
  width: 20px;
  height: 3px;
  border-radius: 3px;
  background-color: #ffffff;
  transform: translateY(-50%);
  transition: opacity 0.2s ease-in-out;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 14px;
  width: 20px;
  height: 3px;
  border-radius: 3px;
  background-color: #ffffff;
  transition: transform 0.2s ease-in-out, top 0.2s ease-in-out, bottom 0.2s ease-in-out;
}
.burger::before {
  top: 18px;
}
.burger::after {
  bottom: 18px;
}
.burger--active {
  transform: rotate(0deg);
